import React, { Fragment } from "react";
import { AccessControlTypes, Tag } from "../../types";
import { Button } from "antd";
import {
  ModalContent,
  ModalOpenButton,
  useModalContext,
} from "components/ui/Modal";
import { Form, FormContentBase } from "components/ui/Form/Form";

export default function NewTagModal() {
  const { onOk, form } = useModalContext();

  function handleOk() {
    onOk("/tag", "create", {} as Tag);
  }

  return (
    <Fragment>
      <ModalOpenButton requiredRoles={["Administrator"]} requiredAccessControls={AccessControlTypes.Create} allowedWithOneWayGitSync={false}>
        <Button children="Create New Tag" />
      </ModalOpenButton>
      <ModalContent title="Create new tag" onClickOk={handleOk} formName="create_tag_form">
        <Form schemaName="tag">
          <FormContentBase
            name="create_tag_form"
            form={form}
            preserve={false}
          />
        </Form>
      </ModalContent>
    </Fragment>
  );
}
